<template>
    <div class="">


        <div class="row q-mt-md q-col-gutter-xs">
            <div class="col col-xs-12 col-sm-12 col-md-12 self-center q-px-md">
                <div class="q-mx-md text-h6 text-center text-weight-bolder">{{ halaman }}</div>
                <!-- <q-separator class="" color="orange" inset /> -->
            </div>

            <div class="col col-xs-12 col-sm-12 col-md-4 q-px-md self-center">
                <q-select filled v-model="model" use-input input-debounce="0" label="Masyarakat / OPD / Sub OPD" :options="options"
                    @filter="filterFn" :dense="true">
                    <template v-slot:no-option>
                        <q-item>
                            <q-item-section class="text-grey">
                                No results
                            </q-item-section>
                        </q-item>
                    </template>
                </q-select>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-4 q-px-md self-center">
                <q-select filled v-model="model" use-input input-debounce="0" label="Simple filter" :options="options"
                    @filter="filterFn" :dense="true">
                    <template v-slot:no-option>
                        <q-item>
                            <q-item-section class="text-grey">
                                No results
                            </q-item-section>
                        </q-item>
                    </template>
                </q-select>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-3 q-px-md self-center">
                <q-input class="self-center" filled v-model="text" label="Pencarian" :dense="true">
                    <template v-slot:append>
                        <q-icon v-if="text !== ''" name="close" @click="text = ''" class="cursor-pointer" />
                        <q-icon name="search" />
                    </template>
                </q-input>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-1 q-px-md self-center">
                <q-btn round @click="mdl_add = true" color="black" icon="add" />
            </div>
        </div>







        <div class="row q-mt-md q-mx-md">
            <!-- <div class="col col-xs-12 col-sm-12 col-md-12 self-center">
                <div class="text-h6">Halaman</div>
            </div> -->

            <div class="col col-xs-12 col-sm-12 col-md-12 self-center">
                <q-markup-table class="my-sticky-dynamic" flat bordered separator="cell">
                    
                    <thead>
                        <tr class="bg-blue-1">
                            <th class="text-center" style="width:10%;">No</th>
                            <th class="text-center" style="width:10%;">Urutan</th>
                            <th class="text-center" style="width:40%;">Uraian</th>
                            <th class="text-center" style="width:10%;">Tipe Menu</th>
                            <th class="text-center" style="width:20%;">Icon</th> 
                            <th class="text-center" style="width:10%;">Act</th>
                        </tr>
                    </thead>
                    <tbody>
                        <template v-for="(item, index) in listData" :key="item.id">
                            <tr class="bg-orange-3">
                                <td class="text-center">{{index+1}}.</td>
                                <!-- <td class="text-left text-no-wrap" > <span style="font-size:12px;">{{item.title}}</span>  <br> <span class="text-brown-5" style="font-size:11px;">{{item.alamat}}</span> </td> -->
                                <td class="text-left" >{{item.urutan}} </td>
                                <td class="text-left" >{{item.title}} </td>
                                <td class="text-left" style="font-size:10px;">{{renameType(item.type)}}</td>
                                <td class="text-left" style="font-size:25px;"> <q-icon :name="item.icon" /> <span style="font-size:10px;">{{item.icon}}</span> </td>
                                <td class="text-center">
                                    <div>
                                        <q-btn-group push> 
                                            <q-btn push color="blue" icon="subdirectory_arrow_left" @click="mdl_add = true, form.jenis=2, form.parrent = item.id" :disabled="checkType(item.type)" size="sm">
                                                <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Tambah Data Submenu </q-tooltip>                                            
                                            </q-btn>  
                                            <q-btn push color="warning" icon="edit" class="text-black" size="sm" @click="mdl_edit=true, getData(item)">
                                                <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Ubah </q-tooltip>                                            
                                            </q-btn>
                                            <q-btn push color="negative" icon="delete" size="sm" @click="mdl_del=true, getData(item)">
                                                <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Hapus </q-tooltip>
                                            </q-btn>
                                        </q-btn-group> 
                                    </div>                                
                                </td>
                            </tr>

                            <template v-for="(item2, index) in item.subItem" :key="item2.id">
                                <tr class="bg-amber-1">
                                    <td class="text-center"> </td>
                                    <td class="text-left" >{{item2.urutan}} </td>
                                    <td class="text-left" >{{item2.title}} </td>
                                    <td class="text-left" style="font-size:10px;">{{renameType(item2.type)}}</td>
                                    <td class="text-left" style="font-size:25px;"> <q-icon :name="item2.icon" /> <span style="font-size:10px;">{{item2.icon}}</span> </td>
                                    <td class="text-center">
                                        <div>
                                            <q-btn-group push> 
                                                <q-btn push color="blue" icon="subdirectory_arrow_left"  :disabled="checkLength(item2.subItem)" size="sm">
                                                    <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Tambah Data Submenu </q-tooltip>                                            
                                                </q-btn>  
                                                <q-btn push color="warning" icon="edit" class="text-black" size="sm" @click="mdl_edit=true, getData(item2)">
                                                    <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Ubah </q-tooltip>                                            
                                                </q-btn>
                                                <q-btn push color="negative" icon="delete" size="sm" @click="mdl_del=true, getData(item2)">
                                                    <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Hapus </q-tooltip>
                                                </q-btn>
                                            </q-btn-group> 
                                        </div>                                
                                    </td>
                                </tr>
    
                            </template>

                        </template>
 
                    </tbody>
                </q-markup-table>
            </div>
            
 


        </div>

        <q-dialog persistent v-model="mdl_add" >
            <q-card style="width: 80vw">
                <q-card-section class="bg-cyan">
                    <div class="text-overline">INPUT</div>
                </q-card-section>

                <q-separator />
                <form @submit.prevent="addData()">
                    <q-card-section style="max-height: 50vh;" class="scroll">
                        <div class="row q-col-gutter-xs">
                            <div class="col-md-12 col-sm-12 col-xs-12"> 
                                <small>*Title</small>
                                <q-input class="self-center" outlined v-model="form.title" label="Title" :dense="true"/>  
                            </div>                        
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <small>*Icon (<a href="https://fonts.google.com/icons" target="_blank">referensi</a>)</small>
                                <q-input class="self-center" outlined v-model="form.icon" label="Icon" :dense="true"/>   
                            </div>
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <small>*Route</small>
                                <q-input class="self-center" outlined v-model="form.route" label="route" :dense="true"/>   
                            </div>
                            <div class="col-md-12 col-sm-12 col-xs-12"> 
                                <small>*Tipe Menu</small>
                                <q-select outlined v-model="form.type" :options="$store.state.type" label="type" option-value="id" option-label="uraian" emit-value map-options :dense="true" />
                            </div>  
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <small>*Urutan</small>
                                <q-input class="self-center" outlined v-model="form.urutan" type="number" label="Urutan" :dense="true"/>   
                            </div> 
                        </div>
                    </q-card-section>
                    <q-separator />
                    <q-card-actions align="right"> 
                        <q-btn-group push>
                            <q-btn push type="submit" color="primary" label="Tambah" icon="add" size="sm"/>
                            <q-btn push color="negative" label="Batal/Keluar" icon="close" size="sm" v-close-popup/> 
                        </q-btn-group>                    
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>

        <q-dialog persistent v-model="mdl_edit" >
            <q-card style="width: 80vw">
                <q-card-section class="bg-amber">
                    <div class="text-overline">EDIT</div>
                </q-card-section>

                <q-separator />
                <form @submit.prevent="editData(form)">
                    <q-card-section style="max-height: 50vh;" class="scroll">
                        <div class="row q-col-gutter-xs">
                            <div class="col-md-12 col-sm-12 col-xs-12"> 
                                <small>*Title</small>
                                <q-input class="self-center" outlined v-model="form.title" label="Title" :dense="true"/>  
                            </div>                        
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <small>*Icon (<a href="https://fonts.google.com/icons" target="_blank">referensi</a>)</small>
                                <q-input class="self-center" outlined v-model="form.icon" label="Icon" :dense="true"/>   
                            </div>
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <small>*Route</small>
                                <q-input class="self-center" outlined v-model="form.route" label="route" :dense="true"/>   
                            </div>
                            <div class="col-md-12 col-sm-12 col-xs-12"> 
                                <small>*Tipe Menu</small>
                                <q-select outlined v-model="form.type" :options="$store.state.type" label="type" option-value="id" option-label="uraian" emit-value map-options :dense="true" />
                            </div>  
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <small>*Urutan</small>
                                <q-input class="self-center" outlined v-model="form.urutan" type="number" label="Urutan" :dense="true"/>   
                            </div> 
                        </div>
                    </q-card-section>
                    <q-separator />
                    <q-card-actions align="right"> 
                        <q-btn-group push>
                            <q-btn push type="submit" color="primary" label="Tambah" icon="add" size="sm"/>
                            <q-btn push color="negative" label="Batal/Keluar" icon="close" size="sm" v-close-popup/> 
                        </q-btn-group>                    
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>

        <q-dialog v-model="mdl_del" persistent>
            <q-card>
                <q-card-section class="row items-center">
                <q-avatar icon="signal_wifi_off" color="primary" text-color="white" />
                <span class="q-ml-sm">You are currently not connected to any network.</span>
                </q-card-section>

                <form @submit.prevent="removeData(form)">
                    <q-card-actions align="right">
                        <q-btn flat label="Hapus" type="submit" color="primary" />
                        <q-btn flat label="Cancel" color="negative" v-close-popup />
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>        
        
    </div>
</template>

<script>
// @ is an alias to /src 
import FETCHING from "../../../library/fetching"; 
import { Notify } from 'quasar'


export default {
    data() {
        return {
            mdl_add: false,
            mdl_edit: false,
            mdl_del: false,
            halaman: 'Menu',
            options: ['data1', 'data2', 'data3', 'data4'],
            model: '',
            text: '',
            FETCHING : FETCHING,
            form:{
                id          :   '', 
                title       :   '', 
                icon        :   '', 
                route       :   '', 
                type        :   0, 
                jenis       :   1, 
                parrent     :   null, 
                urutan      :   0
            },
            listData:[],
        }
    },

    methods: {
        filterFn(val, update) {
            if (val === '') {
                update(() => {
                    console.log(val); 
                })
                return
            }
        },

        getView(){
            fetch(this.$store.state.url.M_MENU+'view', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                // body: JSON.stringify(this.form)
            })
            .then(res => res.json())
            .then(res_data => { 
                this.listData = res_data  
                console.log(res_data);
                 
            });                
        },

        addData(){  
            fetch(this.$store.state.url.M_MENU+'addData', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
            .then(res => res.json())
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
                this.mdl_add = false
            }); 
        },
        editData(){ 
            // console.log(this.form);
            fetch(this.$store.state.url.M_MENU+'editData', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
            .then(res => res.json())
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
                this.mdl_edit = false
            }); 
        },
        removeData(){ 
            // console.log(this.form.id);
            fetch(this.$store.state.url.M_MENU+'removeData', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
            .then(res => res.json())
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
                this.mdl_del = false
            }); 
        },

        getData(data){
            // console.log(data);       
            this.form.id        = data.id
            this.form.title     = data.title
            this.form.icon      = data.icon
            this.form.route     = data.route
            this.form.type      = data.type
            this.form.jenis     = data.jenis
            this.form.parrent   = data.parrent
            this.form.urutan    = data.urutan
        },

        berhasil(){
            Notify.create({
            message: 'BERHASIL',
            // caption: '5 minutes ago',
            icon:'check',
            color: 'secondary' ,
            position:'center',
            timeout: 2000 })
        },


        // ================================================ PROPERTY ================================================ 
        checkLength(data){
            if (data.length <= 0) {
            return true
            } else {
            return false
            }
        },        
        renameType(data){
            if (data == 0) {
            return 'Single Menu'
            } else {
            return 'Multy Menu'
            }
        },
        checkType(data){
            if (data == 0) {
            return true
            } else {
            return false
            }
        },        
        
        // ================================================ PROPERTY ================================================ 




        // ================================================ FILTER ================================================ 
        cariProv : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_PRO = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "provinsi", {cari : val})
                //   console.log(this.form.prov);
                //   this.getView()
                }
              })
        },
        cariKab : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_KAB = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "kabupaten", {cari : val, prov_id:this.form.prov  })
                }
              })
        },
        cariKec : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_KEC = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "kecamatan", {cari : val, kab_id:this.form.kab  })
                }
              })
        },
        cariDes : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_DES = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "desa", {cari : val, kec_id:this.form.kec  })
                }
              })
        },
        // ================================================ FILTER ================================================ 
    },
    mounted() {
        this.getView()
        this.$store.commit('test');
    },

}
</script>
